import pageStatus from "../../components/pageStatus/pageStatus.vue"
import searchHeader from "../../components/searchHeader/searchHeader.vue"

export default{
	data() {
		return {
			locationShow: false,
			publishShow: false,
			locationFlag: false,
			searchValue: '',
			
			currentId1:'',
			currentId2:'',
			currentIndex1:'',/* 一级被选中 */
			currentIndex2:'',/* 二级被选中 */
			categroys:{},
			categoryActive: 0,
			categroys2:{},
			categroys3:{},
		}
	},
	components:{
		searchHeader,
	},
	methods:{
		handleSearch(data){
			const searchInfo = this.store.getSearchStore();
			searchInfo.searchValue = data.searchValue;
			this.store.setSearchStore(searchInfo);
			
			//这里没清理掉
			this.searchValue = '';
			uni.switchTab({
				url:'../home/home',
				success:function(e) {
					var page = getCurrentPages()[0];
					if (page == undefined || page == null) {
						return;
					}else {
						page.onLoad();
					}
				}
			})
		},
		initData(){
			this.https({
				url: this.interfaces.categoryInitData,  //POST json传输必须未转字符串
				method: 'POST',
				data: {
				},
				header: {
					'content-type': 'application/json'
				},
				success: ((res) => { 
					this.categroys = res.data.rootCategorys;
					this.currentId1 = 0;
					this.currentId2 = 0;
					this.categroys2=this.categroys[this.currentId1].childCategorys;
					this.categroys3=this.categroys[this.currentId1].childCategorys[this.currentId2].childCategorys;
				})
			});
		},
		handleColumnScroll(index) {
			this.currentId1 = index;
			this.currentIndex1 = Math.max(0, index - 1);
			this.categroys2=this.categroys[index].childCategorys;
		},
		handleRowScroll(index){
			this.currentId2 = index;
			this.currentIndex2 = Math.max(0, index - 1);
			
			this.categroys3=this.categroys[this.currentId1].childCategorys[index].childCategorys;
		},
		handleItem(index){
			const categroyItem =this.categroys[this.currentId1].childCategorys[this.currentId2].childCategorys[index];
			
			const searchInfo = this.store.getSearchStore();
			searchInfo.searchValue = categroyItem.categoryName;
			this.store.setSearchStore(searchInfo);
			
			uni.switchTab({
				url:'../home/home',
				success:function(e) {
					var page = getCurrentPages()[0];
					if (page == undefined || page == null) {
						return;
					}else {
						page.onLoad();
					}
				}
			})
		}
	},
	onLoad() {
		uni.showLoading({title:"加载中..."})
		this.searchValue = '';
		this.initData(); 			
	},
	onPullDownRefresh(){
		uni.showLoading({title:"加载中..."})
		setTimeout(() => {
			this.initData(); 
			uni.stopPullDownRefresh();
		},1000)
	}
}
